import {uniGrid} from '@dcloudio/uni-ui'
import {uniGridItem} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			showList:['考研就业数据','考研结果统计','考研院校统计','考研专业统计']
		}
	},
	components:{
		uniGrid,
		uniGridItem
	},
	methods: {
		detail(e){
			/* console.log(e.detail) */
			let index = e + 1
			uni.navigateTo({
				url:'../content/kaoyanDetail?index='+index+'&name='+this.showList[index-1]
			})
		}
	}
}
